@import './base/_base.css';
@import './components/_quiz-instructions.css';

.firstcol {
    margin-bottom: 25%;
    margin-top: 8%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 5px;
    background-color: white;
    font-size: large;
}

.firstcolL2 {
    margin-bottom: 20%;
    margin-top: 8%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 5px;
    background-color: white;
    font-size: large;
}

.first2col {
    margin-bottom: 25%;
    border-radius: 5px;
    background-color: "#FCF5F5";
    text-align: center;
    flex-direction: column;
    padding: 2;
}

.secondcol {
    margin-top: 500%;
    border-radius: 5px;
    background-color: "#FCF5F5";
    box-shadow: "rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset";
}

.wrong {
    background-color: #E74C3C;
    display: flex;
    columns: 40px;
    justify-content: center;
    align-items: center;
    margin-bottom: 50%;
    border-radius: 5px;
    text-align: center;
    font-size: large;
}

.right {
    columns: 40px;
    background-color: #58D68D;
    justify-content: center;
    align-items: center;
    margin-bottom: 55%;
    border-radius: 5px;
    text-align: center;
    font-size: large;
}

.white {
    columns: 40px;
    display: flex;
    background-color: white;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-bottom: 60%;
    border-radius: 5px;
    font-size: large;
}

.info {
    columns: 40px;
    background-color: white;
    text-align: center;
    margin-bottom: 35%;
    border-radius: 5px;
    font-size: large;
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}

.imginfo {
    height: 15px;
    width: 15px;
}

.wrong2 {
    background-color: #E74C3C;
    display: flex;
    columns: 40px;
    justify-content: center;
    align-items: center;
    margin-bottom: 50%;
    border-radius: 5px;
    text-align: center;
    font-size: large;
}

.right2 {
    columns: 40px;
    background-color: #58D68D;
    justify-content: center;
    align-items: center;
    margin-bottom: 48%;
    border-radius: 5px;
    text-align: center;
    font-size: large;
}

.white2 {
    columns: 40px;
    display: flex;
    background-color: white;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-bottom: 50%;
    border-radius: 5px;
    font-size: large;
}



